// custom resets
html {
	font-size: 112.5%;
	line-height: 1.5;
}
body,
button,
input,
select,
textarea {
	line-height: 1.75;
}

.single-featured-image-header {
	width: 100%;
	max-height: 500px;
	position: relative;

	img {
		object-fit: cover;
		width: 100%;
		height: 500px;
		object-position: center center;
	}
}

.site-main.home > article.page > header.entry-header {
	display: none;
}

.latest-grid-date {
	margin: 3rem 0 1.5rem;
}
.blog .latest-grid header {
	display: none;
}

#primary #main.site-main .latest-grid {
	@media (min-width: 768px) {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 1rem;
		grid-row-gap: 1rem;
		grid-auto-flow: dense;
	}

	article {
		width: 100%;

		&.tag-instagram {
			.entry-footer {
				display: none;
			}
		}
		.entry-header {
			margin-bottom: 0;
		}

		.grid-item-link {
			display: block;
			display: flex;
			flex-direction: column;
			height: 100%;

			.post-date,
			.entry-title {
				box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 1);
				transition: all 0.2s ease;
			}

			&:hover {
				.post-thumbnail {
					opacity: 1;
				}
				.entry-title,
				.post-date {
					box-shadow: inset 0 -1.2rem 0 rgba(153, 204, 51, 0.5),
						0 3px 0 rgba(0, 0, 0, 1);
				}
			}
		}

		.post-thumbnail {
			margin: 0;
			display: block;
			position: relative;
			padding-top: 100%;
			width: 100%;
			height: 0;
			opacity: 0.9;
			transition: opacity 0.2s ease;

			img {
				height: 100%;
				object-fit: cover;
				object-position: center center;
				width: 100%;
				position: absolute;
				top: 0;
				left: 0;
			}
		}

		.entry-footer {
			border-top: 0;
			margin: auto 0 1rem;
			padding: 0;

			.entry-title {
				font-size: 1rem;
			}
		}
	}
}

